import React, { useState } from 'react'
import "../../Styles/pages/CheckoutPage.css"
import { useGlobal } from '../context/GlobalContext';
import { navigate } from 'gatsby';
import stripeLogo from "../../../static/logo/stripe.png"

import paymentsMethodLogo from "../../../static/logo/payments.png"

const usStates = [
  "Alabama", "Alaska", "Arizona", "Arkansas", "California", "Colorado", "Connecticut", "Delaware",
  "Florida", "Georgia", "Hawaii", "Idaho", "Illinois", "Indiana", "Iowa", "Kansas", "Kentucky",
  "Louisiana", "Maine", "Maryland", "Massachusetts", "Michigan", "Minnesota", "Mississippi",
  "Missouri", "Montana", "Nebraska", "Nevada", "New Hampshire", "New Jersey", "New Mexico",
  "New York", "North Carolina", "North Dakota", "Ohio", "Oklahoma", "Oregon", "Pennsylvania",
  "Rhode Island", "South Carolina", "South Dakota", "Tennessee", "Texas", "Utah", "Vermont",
  "Virginia", "Washington", "West Virginia", "Wisconsin", "Wyoming"
];

function CheckoutPage() {
  const { state } = useGlobal();

  const [formData, setFormData] = useState({
    firstName: '',
    lastName: '',
    state: '',
    city: '',
    zipCode: '',
    address: '',
    apartment: '',
    phone: '',
    email: '',
    createAccount: false,
    shipToDifferent: false,
    secondary: {
      city: '',
      zipCode: '',
      address: ''
    }
  });

  const [errors, setErrors] = useState({});

  const handleInputChange = (e) => {
    const { name, value, type, checked } = e.target;
    if (name.startsWith("secondary.")) {
      const key = name.split(".")[1];
      setFormData(prev => ({
        ...prev,
        secondary: {
          ...prev.secondary,
          [key]: value
        }
      }));
    } else {
      setFormData(prev => ({
        ...prev,
        [name]: type === 'checkbox' ? checked : value
      }));
    }
  };

  const validateForm = () => {
    const newErrors = {};
    if (!formData.firstName) newErrors.firstName = "*required";
    if (!formData.lastName) newErrors.lastName = "*required";
    if (!formData.state) newErrors.state = "*required";
    if (!formData.city) newErrors.city = "*required";
    if (!formData.zipCode) newErrors.zipCode = "*required";
    if (!formData.address) newErrors.address = "*required";
    if (!formData.phone) newErrors.phone = "*required";
    if (!formData.email) newErrors.email = "*required";

    if (formData.shipToDifferent) {
      if (!formData.secondary.city) newErrors["secondary.city"] = "*required";
      if (!formData.secondary.zipCode) newErrors["secondary.zipCode"] = "*required";
      if (!formData.secondary.address) newErrors["secondary.address"] = "*required";
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (!validateForm()) return;
    console.log('Order submitted:', formData);
    navigate('/ordersuccess');
  };

  return (
    <div className="checkout-container">
      {/* HEADER & BREADCRUMB */}
      <header className="checkout-header container">
        <h1>Checkout</h1>
        <nav className="breadcrumb">
          <span className="home-link">Home</span>
          <span className="separator">›</span>
          <span className="current">Checkout</span>
        </nav>
      </header>

      {/* FORM CONTENT */}
      <div className="checkout-content container">
        <form onSubmit={handleSubmit} className="billing-form">
          <div className="form-row">
            <div className="form-group">
              <input name="firstName" placeholder="First name**" value={formData.firstName} onChange={handleInputChange} />
              {errors.firstName && <span className="error-text">{errors.firstName}</span>}
            </div>
            <div className="form-group">
              <input name="lastName" placeholder="Last name**" value={formData.lastName} onChange={handleInputChange} />
              {errors.lastName && <span className="error-text">{errors.lastName}</span>}
            </div>
          </div>

          <div className="form-row">
            <div className="form-group">
              <input name="phone" placeholder="Phone Number**" value={formData.phone} onChange={handleInputChange} />
              {errors.phone && <span className="error-text">{errors.phone}</span>}
            </div>
            <div className="form-group">
              <input name="email" type="email" placeholder="Email Address**" value={formData.email} onChange={handleInputChange} />
              {errors.email && <span className="error-text">{errors.email}</span>}
            </div>
          </div>

          {/* COUNTRY IS LOCKED */}
          <div className="form-row">
            <div className="form-group">
              <input value="United States" disabled />
            </div>
            <div className="form-group">
              <select name="state" value={formData.state} onChange={handleInputChange}>
                <option value="">Select State</option>
                {usStates.map(state => (
                  <option key={state} value={state}>{state}</option>
                ))}
              </select>
              {errors.state && <span className="error-text">{errors.state}</span>}
            </div>
          </div>

          <div className="form-row">
            <div className="form-group">
              <input name="city" placeholder="City**" value={formData.city} onChange={handleInputChange} />
              {errors.city && <span className="error-text">{errors.city}</span>}
            </div>
            <div className="form-group">
              <input name="zipCode" placeholder="Zip Code**" value={formData.zipCode} onChange={handleInputChange} />
              {errors.zipCode && <span className="error-text">{errors.zipCode}</span>}
            </div>
          </div>

          <div className="form-group">
            <input name="address" placeholder="Street Address**" value={formData.address} onChange={handleInputChange} />
            {errors.address && <span className="error-text">{errors.address}</span>}
          </div>
          <div className="form-group">
            <input name="apartment" placeholder="Apt, suite, etc. (optional)" value={formData.apartment} onChange={handleInputChange} />
          </div>

          {/* CHECKBOX */}
          <label className="checkbox-label-checkout">
            <input type="checkbox" name="shipToDifferent" checked={formData.shipToDifferent} onChange={handleInputChange} />
            Ship to a different address?
          </label>

          {/* CONDITIONAL SECONDARY FORM */}
          {formData.shipToDifferent && (
            <>
              <div className="form-row">
                <div className="form-group">
                  <input name="secondary.city" placeholder="Secondary City**" value={formData.secondary.city} onChange={handleInputChange} />
                  {errors["secondary.city"] && <span className="error-text">{errors["secondary.city"]}</span>}
                </div>
                <div className="form-group">
                  <input name="secondary.zipCode" placeholder="Secondary Zip Code**" value={formData.secondary.zipCode} onChange={handleInputChange} />
                  {errors["secondary.zipCode"] && <span className="error-text">{errors["secondary.zipCode"]}</span>}
                </div>
              </div>
              <div className="form-group">
                <input name="secondary.address" placeholder="Secondary Address**" value={formData.secondary.address} onChange={handleInputChange} />
                {errors["secondary.address"] && <span className="error-text">{errors["secondary.address"]}</span>}
              </div>
            </>
          )}

          {/* STRIPE ONLY */}
          <div className="payment-section">
            <img src={stripeLogo} alt="Powered by Stripe" style={{ width: "150px", marginTop: "20px" }} />
          </div>

        </form>

       
 <div className="order-summary">
          <div className="summary-header">
            <span className="product-title">Product</span>
            <span className="subtotal-title">Subtotal</span>
          </div>
          
        
          
          {
            state.cart.map((item) => (
              <div className="summary-item" key={item.id}>
                <span className="product-name-checkout">{item.name} × {item.quantity}</span>
                <span className="product-price">${(item.price * item.quantity).toFixed(2)}</span>
              </div>
            ))
          }
          {
            state.cart.length === 0 && (
              <div className="summary-item">
                <span className="product-name-checkout">No Products in Cart</span>
                <span className="product-price">$0.00</span>
              </div>
            )
          }

          <div className="summary-row subtotal-row">
            <span className="subtotal-label">Subtotal</span>
            <span className="subtotal-amount">${state?.checkoutDetails?.orderSummary?.subtotal}</span>
          </div>

          <div className="shipping-section">
            <h4 className="shipping-title">Shipping</h4>
            <div className="shipping-options">
              <label className="radio-label">
                <input
                  type="radio"
                  name="shippingMethod"
                  value="flat"
                  checked
               
                />
                <span className="radio-custom"></span>
                {
                  state?.checkoutDetails?.orderSummary?.shippingCost === 10 ? `Flat rate: $${state?.checkoutDetails?.orderSummary?.shippingCost || 0}`:  `Free shipping: $${state?.checkoutDetails?.orderSummary?.shippingCost || 0}`
                }
              </label>
         
            </div>
          </div>

          <div className="summary-row total-row">
            <span className="total-label">Total</span>
            <span className="total-amount">${state?.checkoutDetails?.orderSummary?.total}</span>
          </div>

          <div className="payment-section">
            {/* <div className="payment-options">
              <label className="radio-label">
                <input
                  type="radio"
                  name="paymentMethod"
                  value="bank"
                  checked={formData.paymentMethod === 'bank'}
                  onChange={handleInputChange}
                />
                <span className="radio-custom"></span>
                Direct bank transfer
              </label>
              <label className="radio-label">
                <input
                  type="radio"
                  name="paymentMethod"
                  value="cash"
                  checked={formData.paymentMethod === 'cash'}
                  onChange={handleInputChange}
                />
                <span className="radio-custom"></span>
                Cash on delivery
              </label>
              <label className="radio-label">
                <input
                  type="radio"
                  name="paymentMethod"
                  value="paypal"
                  checked={formData.paymentMethod === 'paypal'}
                  onChange={handleInputChange}
                />
                <span className="radio-custom"></span>
                PayPal <span className="paypal-info">What is PayPal?</span>
              </label>
            </div> */}
            
                            <img src={paymentsMethodLogo} alt="Powered by Stripe" style={{ width: "70%",  marginInline:"auto", height:"60px", marginTop: "20px" }} />

            {/* <img src={stripeLogo} alt="Powered by Stripe" style={{ width: "100px", marginTop: "20px" }} /> */}
         

          </div>

          <button type="submit" className="place-order-btn" onClick={handleSubmit}>
            Place Order
          </button>
        </div>
      </div>
    </div>
  );
}

export default CheckoutPage;



